.container {
    width:100%;
    margin:0 auto;
    display:flex;
    align-items:center;
    justify-content: space-around;
}
.search .input {
    width:450px;
    height: 20px;
}
.search .input:focus {
    border:1px solid #007BFF;
}

@media only screen and (max-device-width: 1000px) {
    .container {
        width:100%;
        margin:0 auto;
        display:flex;
        align-items:center;
        flex-direction: column;
        justify-content: space-around;
    }
    .search {
        width: 100%;
        display: flex;
        justify-content: center;
    }
    .search .input {
        width:90%;
        height: 20px;
    }
}


@media only screen and (max-device-width: 414px) {
    .container {
        display: flex;
        flex-direction: column;
    }
    .search .input {
        width:270px;
        height: 20px;
        border: 1px solid silver;
    }
}