.modalContainer {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 40px;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.75);
    transition: 0.35s ease-in-out;
    max-height: 100vh;
}
.modal {
    border-radius: 25px;
    box-sizing: border-box;
    background: white;
    box-shadow: 0px 1px 10px rgba(255, 255, 255, 0.35);
    transition: 0.35s ease-in-out;
    height: 400px;
    width: 600px;
}
.modalHeader {
    text-align: center;
}
.modalHeader h3 {
    font-size: 25px;
}
.modalBody {
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.modalFooter {
    width: 30%;
    padding: 25px 0;
    margin: 0 auto;
}
.confirm {
    display: flex;
    justify-content: center;
}
.confirm button {
    border: 0;
    border-radius: .25em;
    padding: 5px 10px;
    background: initial;
    background-color: #3085d6;
    color: #fff;
    font-size: 1.0625em;
    display: inline-block;
    border-left-color: rgb(48, 133, 214);
    border-right-color: rgb(48, 133, 214);
}

@media only screen and (max-device-width: 1100px) {
    .modalContainer {
        width: 100%;
        min-height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        top: -80px;
        left: -35px;
        background: rgba(0, 0, 0, 0.75);
        transition: 0.35s ease-in-out;
        max-height: 100vh;
    }
    .modal {
        box-sizing: border-box;
        box-shadow: 0px 0px 0px rgb(255 255 255 / 0);
        background: none;
        transition: 0.35s ease-in-out;
        width: 320px;
    }
    .modalHeader {
        text-align: center;
    }
    .modalHeader h3 {
        display: none;
    }
    .modalBody {
        width: 80%;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .modalFooter {
        width: 30%;
        padding: 25px 0;
        margin: 0 auto;
    }
    .confirm {
        display: flex;
        justify-content: center;
    }
    .confirm button {
        border: 0;
        border-radius: .25em;
        padding: 5px 10px;
        background: initial;
        background-color: #3085d6;
        color: #fff;
        font-size: 1.0625em;
        display: inline-block;
        border-left-color: rgb(48, 133, 214);
        border-right-color: rgb(48, 133, 214);
    }
}